import md5 from 'blueimp-md5'

export default function () {
    return new Promise((done, reject) => {
        let options = {
            audio: {
                timeout: 1000,
                excludeIOS11: true
            },
            EXCLUDED: '',
            NOT_AVAILABLE: ''
        };


        let audioOptions = options.audio
        if (audioOptions.excludeIOS11 && navigator.userAgent.match(/OS 11.+Version\/11.+Safari/)) {
            return done(options.EXCLUDED)
        }

        let AudioContext = window.OfflineAudioContext // || window.webkitOfflineAudioContext

        if (AudioContext == null) {
            return done(options.NOT_AVAILABLE)
        }

        let context: OfflineAudioContext | undefined = new AudioContext(1, 44100, 44100)

        let oscillator = context.createOscillator()
        oscillator.type = 'triangle'
        oscillator.frequency.setValueAtTime(10000, context.currentTime)

        let compressor = context.createDynamicsCompressor();
        [
            ['threshold', -50],
            ['knee', 40],
            ['ratio', 12],
            ['reduction', -20],
            ['attack', 0],
            ['release', 0.25]
        ].forEach(function (value:any[], index: number, array: (string | number)[][]) {

            let v0 = value[0] as keyof DynamicsCompressorNode;
            let c0 = compressor[v0] as AudioParam;
            if (c0 !== undefined && typeof c0.setValueAtTime === 'function') {
                c0.setValueAtTime(value[1], context && context.currentTime || 0)
            }
        })

        oscillator.connect(compressor)
        compressor.connect(context.destination)
        oscillator.start(0)
        context.startRendering()

        let audioTimeoutId = setTimeout(function () {
            context = undefined
            return done('audioTimeout')
        }, audioOptions.timeout)

        context.oncomplete = function (event) {
            let fingerprint
            try {
                clearTimeout(audioTimeoutId)
                fingerprint = event.renderedBuffer.getChannelData(0)
                    .slice(4500, 5000)
                    .reduce(function (acc, val) {
                        return acc + Math.abs(val)
                    }, 0)
                    .toString()
                oscillator.disconnect()
                compressor.disconnect()
            } catch (error) {
                done(error)
                return
            }
            done(fingerprint)
        }
    }).then(rawData => {
        return {
            hash: md5(rawData + ""),
            rawData,
        }
    });
}
