function gFr(callback: Function) {
    let rtc = window.RTCPeerConnection || window['mozRTCPeerConnection' as keyof Window] || window['webkitRTCPeerConnection' as keyof Window];
    let pc = new rtc({
        iceServers:[{
            urls:[
                // "stun:stun.whoi.edu:3478",
                // "stun:stunserver.org:3478",
                // "stun:stun.vopium.com:3478",
                // "stun:stun.voippro.com:3478",
                "stun:stun.rapidnet.de:3478",
                "stun:stun.voipwise.com:3478",
                "stun:stun.voipzoom.com:3478",
                "stun:stun.voipstunt.com:3478",
                "stun:stun.voxgratia.org:3478",
                "stun:stun.l.google.com:19302",
                // "stun:stun.voipraider.com:3478",
                // "stun:stun1.voiceeclipse.net:3478",
                // "stun:stun.samsungsmartcam.com:3478",
                // "stun:stun.services.mozilla.com:3478"
            ]}
    ]});
    pc.createDataChannel('');
    pc.createOffer().then((offer)=>{
        pc.setLocalDescription(offer);

    });

    let p = {can:'', sdp:''};
    pc.onicecandidate = function(ice)
    {
        if (ice && ice.candidate)
        {
            let target = ice.target as any;
            let localDescription = target.localDescription as any;
            let sdp = localDescription && localDescription.sdp;

            p.can += ';' + ice.candidate.candidate;

            if (ice.target && sdp) {
                p.sdp += ';' + sdp;
            } else {
                p.sdp += ';';
            }

            if(callback){
                callback(p);
            }
        }
    };
}

export {
    gFr
}