import { AppKeyAndSecretKey, Hosts, RiskMgtOptions, Urls } from './const';
// import {encode, decode} from './js-base64'
import { SHA1 } from 'crypto-es/lib/sha1';
import { Base64 } from 'crypto-es/lib/enc-base64';
import { Utf8 } from 'crypto-es/lib/core';
import md5 from 'blueimp-md5';

function encrypt(data: string): string {
    return Base64.stringify(Utf8.parse(data));
}

function decrypt(data: string): string {
    return Utf8.stringify(Base64.parse(data));
}

function getAppkeyAndSecretKey(env: string): {[key: string]: string} {
    env = env || 'production';
    return AppKeyAndSecretKey[env];
}

function getReportUrl(options: RiskMgtOptions) {
    const env = options.env || 'production';
    let host = Hosts[env];
    // 生产环境且用户传入了host 兼容海外
    if (env === 'production' && options.host && Hosts[options.host]) {
        host = Hosts[options.host]
    }
    const { appKey, secretKey } = AppKeyAndSecretKey[env];
    const timestamp = (new Date().valueOf() / 1000).toFixed();
    const sign = md5(appKey + secretKey + timestamp);
    // 随机选择一个接口上报
    const url = options.reportUrls ? options.reportUrls[(Math.floor(Math.random() * options.reportUrls.length))] : Urls.report;
    return `${host}${url}?appkey=${appKey}&seckey=${secretKey}&bussType=${options.bussType}&timestamp=${timestamp}&sign=${sign}`;
}

function getStrategyUrl(options: RiskMgtOptions) {
    const env = options.env || 'production';
    let host = Hosts[env];
    // 生产环境且用户传入了host 兼容海外
    if (env === 'production' && options.host && Hosts[options.host]) {
        host = Hosts[options.host]
    }
    const { appKey, secretKey } = AppKeyAndSecretKey[env];
    const timestamp = (new Date().valueOf() / 1000).toFixed();
    const sign = md5(appKey + secretKey + timestamp);
    return `${host}${Urls.strategy}?appkey=${appKey}&seckey=${secretKey}&bussType=${options.bussType}&timestamp=${timestamp}&sign=${sign}&type=SDK_INIT`;
}

function isMobile(): boolean {
    const ua = navigator.userAgent;
    const isAndroid = ua.indexOf('Android') > -1 || ua.indexOf('Adr') > -1; // android终端
    const isiOS = !!ua.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); // ios终端
    return isAndroid || isiOS;
}

// 是否是低端机
function isLowEnd(): boolean {
    // 1. url中含参数sf2021=1,该参数是春节活动特有参数
    const url = location.href;
    const isSpringFestival = url.indexOf('sf2021=1') !== -1;
    // 2. 判断ua参数
    const ua = navigator.userAgent;
    const isLowPerformance = ua.indexOf('ISLP/1') !== -1 || (ua.indexOf('Kwai') !== -1 && ua.indexOf('ISLP/') === -1);

    return isSpringFestival && isLowPerformance;
}

function sha1Encode(data: string): string {
    return SHA1(data).toString();
}

