/**
 * 如果引用radar，会导致整包引入&与业务冲突。所以摘取了radar部分上报代码，自定义上报。
 */

export function compose(middlewares: any[]) {
    return function (context: any, next?: any) {
        let index = -1;
        return dispatch(0);

        function dispatch(i: number): any {
            if (i <= index) {
                return new Error('`next` 被重复调用');
            }
            index = i;
            let fn: any | undefined = middlewares[i];
            if (i === middlewares.length) fn = next;
            if (!fn) return;
            try {
                return fn(context, dispatch.bind(null, i + 1));
            } catch (err) {
                return err;
            }
        }
    };
}

export class MiddlewareManager {
    middlewares: any[] = [];

    constructor(...middleware: any[]) {
        this.middlewares.push(...middleware);
    }

    /**
     * 添加使用一个新的中间件
     * @param middlewares
     */
    use(...middlewares: any[]) {
        const defaultMiddleware = this.middlewares.pop();
        this.middlewares.push(...middlewares);
        this.middlewares.push(defaultMiddleware as any);
    }

    /**
     * 执行所有已添加的中间件
     * @param context
     * @returns
     */
    invoke(context: any) {
        const fn = compose(this.middlewares);
        return fn(context);
    }
}

let riskRadarObj: riskRadar;
export default class riskRadar {
    private projectId = '4104e6fdd6';
    private weblog: any = {};
    private radarSessionIdNext: string = '';

    constructor(params: any) {
        this.weblog = params.weblog;

        // @ts-ignore
        this.currentUrlPackage = this.weblog.currentUrlPackage;
        this.radarSessionIdNext = this.uuid();
        riskRadarObj = this;
    }

    getCommonData = () => {
        const { commonPackage: { app_package } } = this.weblog;
        const h5_extra_attr = JSON.stringify({
            ...this.weblog.commonPackage.getH5ExtraAttr(),
            app_version_name: app_package.version_name,
            url: location.href,
            hash: location.hash,
            online: navigator?.onLine, // 是否联网
            downlink: navigator?.connection?.downlink,
            rtt: navigator?.connection?.rtt,
        });
        // @ts-ignore
        const url_package = this.currentUrlPackage && this.currentUrlPackage.toJSON();
        const project_id = this.projectId;
        return {
            h5_extra_attr,
            url_package,
            app_version_name: app_package.version_name,
            project_id,
        };
    }

    private uuid() {
        return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, (c: string) => {
            const r = Math.random() * 16 | 0;
            const v = c === 'x' ? r : (r & 0x3 | 0x8);
            return v.toString(16);
        });
    }

    getUAList() {
        const ua = window.navigator.userAgent;
        const uaList: { [key: string]: string; } = {};
        ua.split(' ').forEach(item => {
            const [key, value] = item.split('/');
            if (key && value) {
                uaList[key] = value;
            }
        });
        return uaList;
    };

    // http上报新链路
    reportByHttp(event: any) {
        const commonData = this.getCommonData();
        const commonLogData = {
            project_id: this.projectId,
            radar_session_id: this.radarSessionIdNext,
            h5_extra_attr: commonData.h5_extra_attr,
        };

        const common: string | any[] = [];

        const dimension = event;
        common.push( dimension );

        if (Array.isArray(common) && common.length) {
            this.collectLog(commonLogData, common, 'radarSDKSupplement');
        }
    }

    collectLog(commonLogData: any, logList: any, serviceName: string) {

        const send = this.weblog.sendByHttp || this.weblog.collect;
        const KEY = 'radar_log';

        send.call(this.weblog, 'RADAR', {
            name: KEY,
            params: {
                ...commonLogData,
                data: [...logList],
            },
            serviceName,
        });
    }

    // 用户定义的事件
    public event(dimension: any, value?: object) {

        const uaList = this.getUAList();
        // 处理extra_info
        if (dimension.extra_info && dimension.extra_info instanceof Object) {
            dimension.extra_info = JSON.stringify(dimension.extra_info);
        }
        // 处理yoda_version
        if (dimension.yoda_version === undefined) {
            dimension.yoda_version = uaList.Yoda || '';
        }
        // 处理webview_type
        if (dimension.webview_type === undefined) {
            dimension.webview_type = this.getWebViewType();
        }
        const log = {
            key: 'event',
            value,
            dimension,
        };

        this.logCollectNext(log);
    }

    logCollectNext(incomeKv: any) {
        let kv = deepClone(incomeKv);

        // this.decorateLog(kv);

        // 获取公共数据
        const commonData = this.getCommonData();
        const app_version_name = commonData.app_version_name;
        delete commonData.app_version_name;

        // 将上报内容中加入公共数据
        const logMiddleWare = new MiddlewareManager((context: any, next: any) => {
            context.dimension.app_version_name = app_version_name;
            context = {
                ...context,
                ...commonData,
                event_client_timestamp: Date.now(),
                event_trigger_source: 'H5',
                radar_session_id: this.radarSessionIdNext,
            };
            return context;
        });

        logMiddleWare.use((context: any, next: any) => {
            // 增加自增id，统计丢失率
            if (context.key && context.key === 'load') {
                context.dimension = {
                    ...context.dimension,
                    load_device_increase_id: genDeviceIncreaseId('LOAD_DEVICE_INCREASE_ID'),
                    load_session_increase_id: genSessionIncreaseId('LOAD_SESSION_INCREASE_ID'),
                };
            }
            if (context.key && context.key !== 'load') {
                context.dimension = {
                    ...context.dimension,
                    other_device_increase_id: genDeviceIncreaseId('OTHER_DEVICE_INCREASE_ID'),
                    other_session_increase_id: genSessionIncreaseId('OTHER_SESSION_INCREASE_ID'),
                };
            }
            return next();
        }, (context: any, next: any) => {
            context.dimension.sample_rate = { load: 1, api: 1, resource: 1, error: 1, event: 1, custom: 1, isHit: true };
            return next();
        });

        kv = logMiddleWare.invoke(kv);
        if (kv instanceof Error) {
            throw kv;
        }

        try {
            window.dispatchEvent(new CustomEvent('get-radar-next-data', {
                detail: {
                    kv: deepClone(kv),
                },
            }));
        } catch (e) {
            //
        }
        // 广播之后删除广播所需的字段，目前仅api有
        delete kv.dimension.broadcast_info;

        this.reportByHttp(kv);
    }

    // 获取webview_type
    getWebViewType = () => {
        const ua = window.navigator.userAgent;
        if (ua.indexOf('Android') !== -1) {
            return 'WebView';
        }
        if (ua.indexOf('iPhone') !== -1) {
            const uaList = this.getUAList();
            if (uaList.Yoda && uaList.WebViewType === 'WK') {
                return 'YodaWKWebView';
            }
            if (!uaList.Yoda && uaList.WebView === 'WKWebView') {
                return 'WKWebView';
            }
            return 'UIWebView';
        }
        return '';
    };

    riskEvent(name: string, event_type: string, extra_info: string) {

        this.event({
            name, //'事件名', // 必填
            // event_type, //'事件类型', // 【可选】
            // category: '业务分类', // 【可选】
            src: location.href,  //'事件相关源文件', // 【可选】
            // result_type: '事件结果', // 【可选】
            // message: '消息', // 【可选】
            extra_info // '额外信息（建议使用json string）' // 【可选】
        });
    }

    pushException(error: any) {
        this.riskEvent(
            'exception',
            '',
            JSON.stringify({
                er: error.toString(),
                st: error.stack
            })
        );
    }
}

// 自增 id 最大值
const maxId = 1e8;
const genDeviceIncreaseId = (key: string) => {
    try {
        if (window && window.localStorage && typeof Storage !== 'undefined' && window.localStorage instanceof Storage) {
            let id = Number(getLocalStorageItem(key)) || 0;
            if (id + 1 > maxId) {
                id = 0;
            }
            setLocalStorageItem(key, id + 1);
            return id;
        }
    } catch (err) {
        //
    }
};

export function deepClone(data: any) {
    return JSON.parse(JSON.stringify(data));
}


/**
 * 获取 localStorage 缓存
 */
 export function getLocalStorageItem(key: string) {
    try {
        if (window && window.localStorage) {
            const value = window.localStorage.getItem(key);
            if (value) {
                try {
                    return JSON.parse(value);
                } catch (err) {
                    return value;
                }
            }
        }
    } catch (err) {
        return null;
    }
    return null;

}
/**
 * 设置 localStorage 缓存
 */
export function setLocalStorageItem(key: string, value: any) {
    try {
        if (window && window.localStorage) {
            window.localStorage.setItem(key, JSON.stringify(value));
            return true;
        }
    } catch (err) {
        return false;
    }
    return false;
}

// 如果没有 localStorage 则使用运行时自增 id，用于非浏览器场景，比如 React Native
const runtimeIdMap: { [key: string]: number } = {};
export const genSessionIncreaseId = (key: string) => {
    if (!runtimeIdMap[key]) {
        runtimeIdMap[key] = 0;
    }
    return runtimeIdMap[key]++;
};

function pushException(error: any) {

    try {
        if(riskRadarObj && error){
            riskRadarObj.pushException(error);
        }
    } catch (error) {
    }

}

export {
    riskRadar,
    pushException,
}
