import md5 from 'blueimp-md5';

import { riskRadar } from './risk-logger';

let urlChangeEventId = 'hashchange';
export default class RiskMgt extends BasePlugin {
    declare weblog: HttpWeblogInstance;
    static override key = 'risk-mgt';
    private beginTime = navigationTiming && navigationTiming.fetchStart
        || new Date().valueOf();
    private options: RiskMgtOptions = {} as RiskMgtOptions;
    private touchStartObject: { [key: number]: any } = {}; // 多个触点touchStart的数据
    private touchMoveObject: { [key: number]: any } = {}; // 多个触点touchMove的数据
    private mousedown: boolean = false;  // 仅在鼠标按下后才会收集
    private mouseDownObject: { [key: string]: any } = {};
    private mouseMoveArray: Array<{ [key: string]: any }> = [];
    private logQueue : any = {};
    private reportUrl: string = ''; // 初始化时随机选择一个上报接口
    // 延迟发送定时器
    private batchWaitTimer: any;
    private battery: any | undefined;
    private estimate: StorageEstimate | undefined;
    private firstSend: boolean = true;  // 是否第一次上报

    private encryptDecryptWorker: any;
    private encryptFailedFunc: Function = () => { };
    private encryptSuccessFunc: Function = () => { };
    private decryptFailedFunc: Function = () => { };
    private decryptSuccessFunc: Function = () => { };
    private auH: string = '';
    private rtcH: string = '';
    private rtcS: string = '';

    private ts: { [key: string]: number } = {};

    private cnt: number = 0;
    private loadTime: number = 0;
    private riskRadar: any = undefined;

    private location: any = {};
    private pers: any = [];

    constructor(options: RiskMgtOptions) {
        super();
        this.options = options as RiskMgtOptions || {};
        this.ts['cs'] = new Date().getTime();


        audioFP().then(({ hash }) => {
            this.auH = hash;
            this.ts['cae'] = new Date().getTime();
        }).catch(error => {
            this.pushException(error);
        });

        try {
            gFr((v: any) => {
                this.rtcH = v.can as string;
                this.rtcS = v.sdp as string;
                this.ts['cre'] = new Date().getTime();
            });
        } catch (error) {
            this.pushException(error);
        }

        this.ts['ce'] = new Date().getTime();
        this.loadTime = (new Date().getTime() - navigationTiming.navigationStart);

        try {
            getAllPermissions((pers: [any])=>{
                this.pers = pers;
            });
        } catch (error) {
            
        }

        getLocation((location: any)=>{
            this.location = location
        })
    }

    override apply(weblog: HttpWeblogInstance) {
        this.ts['as'] = new Date().getTime();

        // 必传参数校验
        if (!this.verifyParams()) return;
        // 低端机不采集
        if (isLowEnd()) return;
        if (!weblog?.collect) return;
        this.weblog = weblog;

        // 默认值赋值
        this.options.switch = true;
        this.options.wait = 0;
        this.options.maxBatchLength = 10;
        this.options.enableNative = false;

        try {
            this.riskRadar = new riskRadar({ weblog: this.weblog });
            this.riskRadar.riskEvent && this.riskRadar.riskEvent('init', '', { ts: '' + this.loadTime });
        } catch (error) {
            this.pushException(error);
        }

        try {
            this.createEncryptDecryptWorker();
        } catch (error) {
            this.pushException(error);
        }

        const callbackFn = () => {
            this.sendCoreData()
            .then(async _ => {
                try {
                    // 电量变化上报
                    if ((navigator as any).getBattery) {
                        const battery = await (navigator as any).getBattery();
                        if (battery) {
                            this.battery = battery;
                            this.battery.addEventListener('chargingchange', () => {
                                const moduleSection = this.generateModuleSection({
                                    type: 'CHARGINGCHANGGE',
                                    level: this.battery.level,
                                    timestamp: new Date().valueOf(),
                                }, 45);
                                this.send(moduleSection);
                            })
                        }
                    }
                } catch (e) {
                    error(e)
                    this.pushException(e);
                }

                // 存储配额与使用量
                try {
                    if (navigator.storage) {
                        const estimate = await navigator.storage.estimate()
                        if (estimate) {
                            this.estimate = estimate;
                        }
                    }
                } catch (e) {
                    error(e);
                    this.pushException(e);
                }
                // 发送PV信息
                if (this.options.switch) {
                    // 上报完整数据
                    this.sendWholeData();
                    // 注册事件
                    this.addEvent();
                    // 注册业务事件
                    this.addActivityEvent();
                }
            })
        }
                
        try {
            if (this.options.switchAsync) {
                callbackFn();
                this.getConfig(() => { });
            } else {
                this.getConfig(callbackFn);
            }
        } catch (error) {
            this.pushException(error);
        }

        this.ts['ae'] = new Date().getTime();
    }

    // 上报核心数据
    private async sendCoreData() {
        if (this.options.switch) {
            // 事件上报
            const yoda = this.weblog.Utils.yoda;
            // console.log('yoda:', yoda);
            if (this.options.enableNative && yoda) {
                const { canUse } = await yoda.invoke('tool.canIUse', {
                    namespace: 'weapon',
                    name: 'recordJSScene',
                }) as { canUse: boolean };
                // console.log('yoda caniuse result:', result, canUse);
                if (canUse) {
                    yoda.invoke('weapon.recordJSScene', {
                        bussType: this.options.bussType,
                        jsVersion: RM_VERSION,
                        taskType: this.options.taskType,
                        subTaskType: '1111',   // 页面进入
                    });
                }
            }
            this.reportUrl = getReportUrl(this.options);
            const moduleSection = this.generateCoreModuleSection({
                type: 'PAGE_ENTER',
                timestamp: new Date().valueOf(),
                initTime: this.beginTime,    // sdk初始化时间
            }, '21');
            this.send(moduleSection, true, CoreDataLoggerId);
        }
    }

    // 发送完整数据
    private async sendWholeData() {
        if (this.options.switch) {
            const moduleSection = this.generateModuleSection({
                type: 'PAGE_ENTER',
                timestamp: new Date().valueOf(),
                initTime: this.beginTime,    // sdk初始化时间
            }, '21');
            this.send(moduleSection);
        }
    }
    // 注册事件监听
    private addEvent() {
        if (isMobile()) {
            // 是否需要记录多个手指
            window.addEventListener('touchstart', this.touchStartEvent, true);
            window.addEventListener('touchmove', this.touchMoveEvent, true);
            window.addEventListener('touchend', this.touchEndEvent, true);
        } else {
            // 是否需要记录哪个按键
            window.addEventListener('mousedown', this.mouseDownEvent, true);
            window.addEventListener('mousemove', this.mouseMoveEvent, true);
            window.addEventListener('mouseup', this.mouseUpEvent, true);
        }
        // 判断是否支持history API，优先使用history API。这是因为hash改变时也会触发popstate
        urlChangeEventId = this.weblog.Utils.ua.supportsPushState() ? 'popstate' : 'hashchange';
        addMonitor(window, urlChangeEventId, this.urlChange);
        addMonitor(window, 'onpagehide' in window ? 'pagehide' : 'beforeunload', this.beforeunload);

        // 键盘按下触发或者游戏控制器第一次连接或者第一次按下游戏键/摇杆的时候触发。
        window.addEventListener('keydown', this.keyDownHandler, true);
        window.addEventListener("gamepadconnected", this.gamepadConnectHandler, true);

        document.addEventListener('copy', event => {
            //剪切板数据
            const moduleSection = this.generateModuleSection({
                type: 'copy',
                timestamp: new Date().valueOf(),
            }, 44);
            this.send(moduleSection)
        });
        document.addEventListener('cut', event => {
            //剪切板数据
            const moduleSection = this.generateModuleSection({
                type: 'cut',
                timestamp: new Date().valueOf(),
            }, 44);
            this.send(moduleSection)
        });
        document.addEventListener('paste', (event) => {
            //剪切板数据
            const moduleSection = this.generateModuleSection({
                type: 'paste',
                timestamp: new Date().valueOf(),
            }, 44);
            this.send(moduleSection)
        });

        document.addEventListener('selectionchange', event => {
            //选中文本数据
            if (document.getSelection()?.toString() != '') {
                // console.log(document.getSelection()?.toString());
                const moduleSection = this.generateModuleSection({
                    type: 'selectionchange',
                    timestamp: new Date().valueOf(),
                }, 44);
                this.send(moduleSection)
            }
        });
    }


    private addActivityEvent() {
        const addEventListener = (elementName: string, callback: () => void) => {
            try {
                if (!elementName || elementName === '' || !callback) {
                    return;
                }
                let list = document.getElementsByClassName(elementName);
                var element;
                if (list.length === 0) {
                    let idElement = document.getElementById(elementName);
                    if (!idElement) {
                        return;
                    }
                    element = idElement;
                } else {
                    element = list[0];
                }
                if (!element) {
                    return;
                }
                element.addEventListener('click', callback);
            } catch (error) {
                this.pushException(error);
            }
        };

        window.onload = () => {
            let elements: { [key: string]: number } = {
                'like-item': 110,            // 点赞收藏
                'uninterested-item': 111,    // 不感兴趣
                'report-item': 112,          // 举报
                'profile-user-name': 113,    // 点击头像昵称-进入主页
                'short-video-follow': 114,   // 关注
                'submit-button': 115,        // 发评论/回复发送按钮
                'video-switch-last': 116,    // 上一条视频
                'video-switch-next': 117     // 下一条视频
            };
            for (let name in elements) {
                addEventListener(name, () => {
                    const moduleSection = this.generateModuleSection({
                        type: name,
                        timestamp: new Date().valueOf(),
                    }, elements[name]);
                    this.send(moduleSection);
                });
            }
        }
    }


    private mouseDownEvent = (event: MouseEvent) => {
        this.mousedown = true;
        this.mouseDownObject = this.pickMouseEventProps(event)
        this.ts['ms'] = new Date().getTime();
    }

    private mouseMoveEvent = (event: MouseEvent) => {
        if (!this.mousedown) return;
        this.mouseMoveArray.push(this.pickMouseEventProps(event));
        this.ts['mm'] = new Date().getTime();
    }

    private mouseUpEvent = (event: MouseEvent) => {
        if (!this.mousedown) return;
        const mouseup = this.pickMouseEventProps(event)

        const mouseInfo = {
            mouseDown: {},
            mouseMove: [] as any[],
            mouseUp: {},
        };
        mouseInfo.mouseDown = this.mouseDownObject;
        mouseInfo.mouseMove = this.mouseMoveArray;
        mouseInfo.mouseUp = mouseup;
        const formatData: elementData | undefined = this.dataFormat(event);
        if (formatData) {
            let moduleSection;
            if (this.mouseMoveArray.length === 0) {
                moduleSection = this.generateModuleSection({
                    type: 'CLICK',
                    timestamp: new Date().valueOf(),
                    ...formatData,
                }, '19');
            } else {
                moduleSection = this.generateModuleSection({
                    type: 'MOUSE',
                    timestamp: new Date().valueOf(),
                    ...formatData,
                    mouseInfo,
                }, '22');
                // console.log('mouse', moduleSection)
            }
            this.send(moduleSection);
        }
        this.mousedown = false;
        this.mouseDownObject = {};
        this.mouseMoveArray = [];
        this.ts['me'] = new Date().getTime();
    }

    private pickMouseEventProps = (event: MouseEvent) => {
        // @ts-ignore
        const { clientX, clientY, altKey, button, buttons, ctrlKey, metaKey, pageX, pageY, region, relatedTarget, shiftKey, which, webkitForce } = event;

        return {
            clientX: Number(clientX.toFixed(2)),
            clientY: Number(clientY.toFixed(2)),
            altKey,
            button,
            buttons,
            ctrlKey,
            metaKey,
            pageX: Number(clientX.toFixed(2)),
            pageY: Number(clientY.toFixed(2)),
            region,
            relatedTarget,
            shiftKey,
            which,
            webkitForce,
            timestamp: new Date().valueOf(),
        }
    }

    private keyDownHandler = (event: KeyboardEvent) => {
        let moduleSection;
        moduleSection = this.generateModuleSection({
            type: 'KEYBOARD',
            timestamp: new Date().valueOf(),
            altKey: event.altKey,
            code: event.code,
            ctrlKey: event.ctrlKey,
            isComposing: event.isComposing,
            key: event.key,
            locale: (event as any).locale || '',
            location: event.location,
            metaKey: event.metaKey,
            repeat: event.repeat,
            shiftKey: event.shiftKey,
        }, '45');
        this.send(moduleSection);
    }

    private gamepadConnectHandler = (event: any) => {
        const moduleSection = this.generateModuleSection({
            type: 'GAMEPADCONNECTED',
            game_pad_id: event.gamepad.id,
            timestamp: new Date().valueOf(),
        }, 44);
        this.send(moduleSection)
    }

    private verifyParams() {
        if (!this.options.bussType || typeof this.options.bussType !== 'string') {
            error('【风控sdk】请设置有效的bussType值!');
            return false;
        }
        if (!this.options.taskType || typeof this.options.taskType !== 'string') {
            error('【风控sdk】请设置有效的taskType值!');
            return false;
        }
        if (!this.options.subTaskType || typeof this.options.subTaskType !== 'string') {
            error('【风控sdk】请设置有效的subTaskType值!');
            return false;
        }

        return true;
    }

    // 服务端拉取配置
    private getConfig = (callback: () => void) => {
        const urlAndData: { [key: string]: any } = {
            data: JSON.stringify({
                device_id: this.weblog.commonPackage.identity_package.device_id,
                sdkver: RM_VERSION,
                pver: '0.0.0',
                hp: this.options.bussType,
                platform: 3,
            }),
            flag: FLAG,
            url: getStrategyUrl(this.options),
        };

        this.encryptFailedFunc = ((urlAndData: any, callback: () => void) => {
            return () => {
                const { url, ...data } = urlAndData;
                data.data = encrypt(data.data);
                this.dataXhr(urlAndData, callback);
            }
        })(urlAndData, callback);

        this.encryptSuccessFunc = ((callback: () => void) => {
            return (urlAndData: any) => {
                this.dataXhr(urlAndData, callback);
            }
        })(callback);

        this.encryptDecryptWorker.postMessage({
            type: 'encrypt',
            info: urlAndData,
        });
    }

    dataXhr(urlAndData: any, callback: () => void) {
        const { url, ...data } = urlAndData;
        const xhr = new XMLHttpRequest();
        xhr.onreadystatechange = () => {
            if (xhr.readyState === 4) {
                if ((xhr.status >= 200 && xhr.status < 300) || xhr.status == 304) {
                    if (xhr.responseText) {
                        const response = JSON.parse(xhr.responseText);
                        if (response.result === 1) {

                            this.decryptFailedFunc = ((responseData: any, callback: () => void) => {
                                return () => {
                                    const config = JSON.parse(decrypt(responseData));
                                    // console.log('config', config);
                                    // 设置参数
                                    this.options.switch = config.switch;
                                    this.options.maxBatchLength = config.maxBatchLength;
                                    this.options.wait = config.wait;
                                    this.options.reportUrls = config?.reportConfig?.reportUrls;
                                    this.options.enableNative = config.enableNative;
                                    callback();
                                }
                            })(response.antispamPluginRsp, callback);


                            this.decryptSuccessFunc = ((callback: () => void) => {
                                return (eventData: any) => {
                                    this.options.switch = eventData.switch;
                                    this.options.maxBatchLength = eventData.maxBatchLength;
                                    this.options.wait = eventData.wait;
                                    this.options.reportUrls = eventData.reportConfig?.reportUrls;
                                    this.options.enableNative = eventData.enableNative;
                                    callback();
                                }
                            })(callback);

                            this.encryptDecryptWorker.postMessage({
                                type: 'decrypt',
                                info: response.antispamPluginRsp,
                            });
                        } else {
                            callback();
                        }
                    }
                } else {
                    console.warn('Request was unsuccessful:' + xhr.status);
                    callback();
                }
            }
        };
        xhr.open('POST', url);
        xhr.send(JSON.stringify(data));
    }

    send(data: any, immediate = false, logId = LoggerId) {
        // console.log('send', data);
        if (!this.options.switch) return;
        if (immediate) {
            this.sendLogs([data] as any, logId);
            return;
        }

        this.batchWaitTimer && clearTimeout(this.batchWaitTimer);
        if (!this.logQueue[logId]) {
            this.logQueue[logId] = [];
        }
        var logs = this.logQueue[logId];
        logs.push(data);
        // 积攒超过最大
        if (logs.length >= (this.options?.maxBatchLength || 0)) {
            this.flush(logId);
            return;
        }

        this.batchWaitTimer = setTimeout(() => {
            this.flush(logId);
        }, this.options.wait);

        ++this.cnt;
        //采样上报
        if(this.cnt % 5 == 0){
            this.riskRadar.riskEvent('cnt', '', { cnt: '' + (this.cnt) });
        }
    }

    flush = (logId = LoggerId) => {
        this.sendLogs(this.logQueue[logId], logId);
        this.logQueue[logId] = [];
    }

    private sendLogs(logs: any, logId = LoggerId) {
        if (!logs || !logs.length) {
            return;
        }

        this.buildLogPackage(logs, logId);
    }

    createEncryptDecryptWorker() {
        this.encryptDecryptWorker = EncryptDecryptWorker();
        let type = 0;
        this.encryptDecryptWorker.onerror = (event: ErrorEvent) => {
            error('worker onerror', event.message);
            type++;
            if (type === 1) {
                this.encryptFailedFunc();
            } else if (type === 2) {
                this.decryptFailedFunc();
                this.encryptDecryptWorker.terminate();
            }
        }
        this.encryptDecryptWorker.onmessageerror = (event: ErrorEvent) => {
            error('worker onmessageerror', event.message)
            type++;
            if (type === 1) {
                this.encryptFailedFunc();
            } else if (type === 2) {
                this.decryptFailedFunc();
                this.encryptDecryptWorker.terminate();
            }
        };
        this.encryptDecryptWorker.onmessage = (event: MessageEvent) => {
            type++;
            if (type === 1) {
                this.encryptSuccessFunc(event.data);
            } else if (type === 2) {
                this.decryptSuccessFunc(event.data);
                this.encryptDecryptWorker.terminate();
            }
        }
    }

    private buildLogPackage(logs: any, logId = LoggerId) {
        this.ts['ls'] = new Date().getTime();

        const { Utils: { cookie } } = this.weblog;
        const source: Item = {} as Item;
        const data: { [key: string]: any } = {};
        data['1'] = this.weblog.commonPackage.identity_package.device_id;
        data['2'] = cookie.getCookie('kpn');
        data['3'] = logId;
        data['4'] = this.weblog.commonPackage.app_package.version_name;
        data['5'] = cookie.getCookie('kpn');
        data['6'] = this.options.bussType;
        data['7'] = this.weblog.commonPackage.app_package.platform;
        data['8'] = this.weblog.commonPackage.app_package.version_code;
        data['9'] = new Date().valueOf();
        data['10'] = '';
        data['11'] = this.options.primaryKey;
        data['12'] = getAppkeyAndSecretKey(this.options.env).appKey;
        data['13'] = this.options.pluginPackageName || '';
        data['14'] = RM_VERSION;

        data.module_section = logs;
        // console.log(data);
        source.flag = FLAG;
        if (this.firstSend) {
            this.firstSend = false;
            const encryptWorker = EncryptWorker();
            encryptWorker.onerror = (event: ErrorEvent) => {
                error('worker onerror', event.message);
                source.data = encrypt(JSON.stringify(data));
                this.weblog.Utils.io.sendData({ data: source, url: this.reportUrl});
            }
            encryptWorker.onmessageerror = (event: ErrorEvent) => {
                error('worker onmessageerror', event.message)
                source.data = encrypt(JSON.stringify(data));
                this.weblog.Utils.io.sendData({ data: source, url: this.reportUrl});
            };
            encryptWorker.onmessage = (event: MessageEvent) => {
                source.data = event.data;
                this.weblog.Utils.io.sendData({ data: source, url: this.reportUrl});
            }
            encryptWorker.postMessage(JSON.stringify(data))
        } else {
            source.data = encrypt(JSON.stringify(data));
            this.weblog.Utils.io.sendData({ data: source, url: this.reportUrl});

        }

        this.ts['le'] = new Date().getTime();
    }

    private touchStartEvent = (event: TouchEvent) => {
        this.ts['ts'] = new Date().getTime();

        const touches = event.touches;
        for (let i = 0; i < touches.length; i++) {
            const { clientX, clientY, identifier, force } = touches[i]!;
            if (this.touchStartObject[identifier]) {
                return;
            }
            this.touchStartObject[identifier] = {
                clientX: Number(clientX.toFixed(2)),
                clientY: Number(clientY.toFixed(2)),
                force,
                timestamp: new Date().valueOf(),
            };
        }
    }

    private touchMoveEvent = (event: TouchEvent) => {
        this.ts['tm'] = new Date().getTime();

        const touches = event.changedTouches;
        for (let i = 0; i < touches.length; i++) {
            const { clientX, clientY, identifier, force } = touches[i]!;
            const data = {
                clientX: Number(clientX.toFixed(2)),
                clientY: Number(clientY.toFixed(2)),
                timestamp: new Date().valueOf(),
                force
            };
            if (this.touchMoveObject[identifier]) {
                this.touchMoveObject[identifier].push(data);
                return;
            }
            this.touchMoveObject[identifier] = [data];
        }
    }

    private touchEndEvent = (event: TouchEvent) => {
        this.ts['te'] = new Date().getTime();

        const touches = event.changedTouches;
        const touchesInfo = [];
        for (let i = 0; i < touches.length; i++) {
            const { clientX, clientY, identifier, force } = touches[i]!;
            const touchItem = {
                touchStart: {},
                touchMove: [],
                touchEnd: {} as {
                    clientX?: number,
                    clientY?: number,
                    timestamp?: number,
                    force?: number,
                },
            };
            const currentTouchStartPointerInfo = this.touchStartObject[identifier];
            const currentTouchMovePointerInfo = this.touchMoveObject[identifier];
            // touchstart info
            if (currentTouchStartPointerInfo) {
                touchItem.touchStart = currentTouchStartPointerInfo;
                delete this.touchStartObject[identifier];
            }
            // touchmove info
            if (currentTouchMovePointerInfo) {
                touchItem.touchMove = currentTouchMovePointerInfo;
                delete this.touchMoveObject[identifier];
            }
            // touchend info
            touchItem.touchEnd = {
                clientX: Number(clientX.toFixed(2)),
                clientY: Number(clientY.toFixed(2)),
                timestamp: new Date().valueOf(),
                force
            };
            touchesInfo.push(touchItem);
        }

        const isClick = touchesInfo[0] && touchesInfo[0].touchMove.length === 0;
        const formatData: elementData | undefined = this.dataFormat(event);
        if (formatData) {
            let moduleSection;
            if (isClick) {
                moduleSection = this.generateModuleSection({
                    type: 'CLICK',
                    timestamp: new Date().valueOf(),
                    ...formatData,
                    // touch event返回的clientX&clientY是undefined,在stringify时会被忽略，手动从touch里拿到对应值
                    clientX: touchesInfo[0]?.touchEnd?.clientX,
                    clientY: touchesInfo[0]?.touchEnd?.clientY,
                }, '19');
            } else {
                moduleSection = this.generateModuleSection({
                    type: 'TOUCH',
                    timestamp: new Date().valueOf(),
                    ...formatData,
                    touchesInfo,
                }, '18');
            }

            this.send(moduleSection);
        }
    }

    private urlChange = () => {
        setTimeout(() => {
            if (!this.weblog) return;
            const { page } = getMappingPage({ url: location.href }, this.weblog.logConfig.urlMap);
            if (page === this.weblog.currentUrlPackage.page) return;

            // 触发旧页面离开
            const now = new Date().valueOf();
            const moduleSectionLeave = this.generateModuleSection({
                type: 'PAGE_LEAVE',
                timestamp: now,
                stay: now - this.beginTime,
            }, '23');
            this.send(moduleSectionLeave);

            // 浏览器历史记录发生变化时，需判断新的url是否满足低端机的要求
            if (isLowEnd()) {
                this.destroy();
                return;
            }

            // 新页面上报
            this.beginTime = now;   // 更新新页面初始时间
            const moduleSectionEnter = this.generateModuleSection({
                type: 'PAGE_ENTER',
                timestamp: now,
                initTime: this.beginTime,    // sdk初始化时间
            }, '21');
            this.send(moduleSectionEnter);
        });
    }

    // 数据组织
    private dataFormat = (event: (MouseEvent | TouchEvent)) => {
        const customData = null;
        const { target, clientX, clientY } = event as (MouseEvent | TouchEvent) & { target: HTMLElement; clientX: number; clientY: number; };
        if (!target) return;
        const { tagName } = target;
        // 顶级标签不采集
        if (['html', 'body'].indexOf(tagName.toLowerCase()) !== -1) return;

        // 采集信息
        const xpath = getXpathFromNode(target);
        const { innerText, nodeName, attributes } = target;

        // 序列化
        const attributesSerialize: { [key: string]: string } = {};
        for (let i = 0; i < attributes.length; i++) {
            const { name, value } = attributes[i]!;
            // 强制采集：自定义属性
            if (name.indexOf('data-') !== -1) {
                attributesSerialize[name] = value;
            } else {
                attributesSerialize[name] = value;
            }
        }

        return { xpath, innerText, nodeName, attributesSerialize, clientX, clientY, customData };
    }

    private generateModuleSection = (options?: any, eventIndex?: any) => {
        this.ts['gs'] = new Date().getTime();

        const { referUrlPackage, Utils: { cookie }, commonPackage: { network_package, device_package, h5_extra_attr, app_package, identity_package }, sessionId } = this.weblog;
        let estimateRate = '';

        if (this.estimate && this.estimate.usage !== undefined && this.estimate.quota !== undefined) {
            estimateRate = (this.estimate.usage / this.estimate.quota).toFixed(2);
        }
        let webglFD = getWeglFdData();
        let canvasFD = getCanvasFdData();
        let webglData = getWeglData(document);
        let ow = 'outerWidth' as keyof Window;
        let iw = 'innerWidth' as keyof Window;

        let oh = 'outerHeight' as keyof Window;
        let ih = 'innerHeight' as keyof Window;

        let nwd = '';
        try {
            nwd = navigator.webdriver.toString();
        } catch (err) {
            this.pushException(err);
        }

        let nw = false;
        try {
            nw = navigator.webdriver;
        } catch (err) {
            this.pushException(err);
        }

        let pha = '0';
        try {
            if (window['_phantom' as keyof Window] || window['phantom' as keyof Window] || window['callPhantom' as keyof Window]) {
                pha = '1';
            }
        } catch (err) {
            this.pushException(err);
        }

        let nw2 = undefined;
        try {
            nw2 = navigator['w' + 'e' + 'b' + 'dr' + '' + 'iv' + 'er' as keyof Navigator];
        } catch (err) {
            this.pushException(err);
        }

        this.ts['ge'] = new Date().getTime();

        let pcs = printCallStack.toString();
        let wfs = getWeglFdData.toString();
        let cfs = getCanvasFdData.toString();
        let mfs = getMathFingerprint.toString();
        let ffs = detectjsFiles.toString();
        let afs = audioFP.toString();
        let grs = gFr.toString();
        let els = eval.toString();
        let ows = Object.getOwnPropertyNames(window).toString();
        // let owss = getObProNameStr().toString();
        
        let kys = Object.keys.toString();
        let cis = document.cookie.toString();
        // let ciss = getdocCoStr();

        const moduleSection: { [key: string]: any } = {
            1: referUrlPackage ? referUrlPackage : '',
            2: location.href,
            3: network_package && network_package.ip,
            4: h5_extra_attr && h5_extra_attr.domain,
            5: sessionId,
            6: document && document.title || '',
            7: cookie.parseCookieString(document && document.cookie || '', true),
            8: getResolution(),
            9: document && document.body && document.body.clientHeight || '',
            10: document && document.body && document.body.clientWidth || '',
            11: device_package && device_package.ua,
            12: device_package && device_package.os_version,
            13: app_package && app_package.language,
            14: device_package && device_package.model,
            16: identity_package && identity_package.user_id,
            17: identity_package && identity_package.device_id,
            24: this.options.taskType,
            25: this.options.subTaskType,
            26: '0',
            28: sha1Encode((identity_package.device_id as string) + (referUrlPackage ? referUrlPackage : '') + getResolution() + device_package.ua + location.href),
            29: new URL(this.reportUrl).pathname,
            30: this.options.host || 'ks',

            // 第三期字段
            // 30: position,
            31: this.battery && this.battery.level,
            32: estimateRate,
            33: navigator.appCodeName,
            34: navigator.appName,
            35: navigator.appVersion,
            36: navigator.platform,
            37: JSON.stringify(navigator.languages),
            //   38: JSON.stringify(navigator.plugins),
            39: (navigator as any).oscpu,
            40: (navigator as any).buildID,
            41: navigator.productSub,
            42: navigator.vendor,
            43: navigator.vendorSub,
            44: navigator.hardwareConcurrency,
            90: this.options.reserved ? this.options.reserved : '',
            50: navigator.maxTouchPoints,
            51: navigator.doNotTrack ? navigator.doNotTrack : '',
            52: navigator.cookieEnabled ? 1 : 0,
            53: navigator.product,
            54: navigator.onLine,
            55: nw ? 1 : 0,
            56: navigator.userAgent,
            57: navigator.language ? navigator.language : '',
            58: getNavigatorPlugins(navigator),
            59: getNavigatorMimeTypes(navigator),
            61: webglData['61'] ? webglData['61'] : '',
            62: webglData['62'] ? webglData['62'] : '',
            63: webglData['63'] ? webglData['63'] : '',
            64: webglData['64'] ? webglData['64'] : '',
            65: webglData['65'] ? webglData['65'] : '',
            66: webglData['66'] ? webglData['66'] : '',
            67: webglData['67'],
            68: webglFD ? webglFD.hash : '',
            69: canvasFD ? canvasFD.hash : '',
            //70-74 预留一些audio使用
            70: this.auH,
            // 75-79 预留一些检测自动化工具
            75: nw2 ? '0' : '1',
            76: nwd,

            77: detectjsFiles(),
            78: { w: getObjPropertys(window), n: getObjPropertys(window.navigator) },
            79: printCallStack(),

            80: (window[ow] - window[iw]) + '',
            81: (window[oh] - window[ih]) + '',
            82: {
                pc: pcs.length, ph: md5(pcs),
                wf: wfs.length, wh: md5(wfs),
                cf: cfs.length, ch: md5(cfs),
                mf: mfs.length, mh: md5(mfs),
                ff: ffs.length, fh: md5(ffs),

                af: afs.length, ah: md5(afs),
                gr: grs.length, gh: md5(grs),

                el: els.length, eh: md5(els),

                ow: ows.length, oh: md5(ows),
                // ox: owss.length, xh: md5(owss),
                ky: kys.length, kh: md5(kys),
                ci: cis.length, ih: md5(cis),
                // cj: ciss.length, jh: md5(ciss),
            },

            //rtc
            85: this.rtcH,
            86: this.rtcS,
            //screen
            87: { w: window.screen.width, h: window.screen.height, c: window.screen.colorDepth, p: window.screen.pixelDepth },
            //math
            88: getMathFingerprint().h,
            //timestamp
            89: this.ts,

            //90 为预留字段

            100: getCookieL(),
            101: getStorage(),
            102: getHisLen(),
            103: getFS(),
            104: {
                ts: (new Date).getTime(),
                cts: (new Date).getTime(),
            },
            105: pha,
            106: this.pers,

            107: new Date().getTimezoneOffset()/60,
            108: this.location ? this.location : {},

            // 109: getCallStack(),
        };

        if (eventIndex) {
            moduleSection[eventIndex] = options;
        }

        return moduleSection;
    }

    private generateCoreModuleSection = (options?: any, eventIndex?: any) => {
        const { referUrlPackage, Utils: { cookie }, commonPackage: { network_package, device_package, identity_package = {} }, sessionId } = this.weblog;
        let canvasFD = getCanvasFdData();

        
        let nwd = '';
        try {
            nwd = navigator.webdriver.toString();
        } catch (err) {
            this.pushException(err);
        }

        let nw = false;
        try {
            nw = navigator.webdriver;
        } catch (err) {
            this.pushException(err);
        }

        let nw2 = undefined;
        try {
            nw2 = navigator['w' + 'e' + 'b' + 'dr' + '' + 'iv' + 'er' as keyof Navigator];
        } catch (err) {
            this.pushException(err);
        }

        let els = eval.toString();
        let cfs = getCanvasFdData.toString();
        let ffs = detectjsFiles.toString();
        let cis = document.cookie.toString();
        
        const moduleSection: { [key: string]: any } = {
            1: referUrlPackage ? referUrlPackage : '',
            2: location.href,
            3: network_package && network_package.ip,
            5: sessionId,
            7: cookie.parseCookieString(document && document.cookie || '', true),
            8: getResolution(),
            11: device_package && device_package.ua,
            16: identity_package && identity_package.user_id,
            17: identity_package && identity_package.device_id,
            28: sha1Encode(identity_package.device_id as string + (referUrlPackage ? referUrlPackage : '') + getResolution() + device_package.ua + location.href),

            // 第三期字段
            // 30: position,
            33: navigator.appCodeName,
            34: navigator.appName,
            35: navigator.appVersion,
            36: navigator.platform,
            //   38: JSON.stringify(navigator.plugins),
            55: nw ? 1 : 0,
            56: navigator.userAgent,
            69: canvasFD ? canvasFD.hash : '',
            // 75-79 预留一些检测自动化工具
            75: nw2 ? '0' : '1',
            76: nwd,

            77: detectjsFiles(),
            79: printCallStack(),
            82: {
                cf: cfs.length, ch: md5(cfs),
                ff: ffs.length, fh: md5(ffs),
                el: els.length, eh: md5(els),
                ci: cis.length, ih: md5(cis),
            },            
            //timestamp
            89: this.ts,
            102: getHisLen(),
            108: this.location ? this.location : {},

            // 109: getCallStack(),
        };

        if (eventIndex) {
            moduleSection[eventIndex] = options;
        }
        return moduleSection;
    }

    private pushException = (error: any) => {
        this.riskRadar && this.riskRadar.pushException(error);
    }

    // 特意改成beforeunload，因为weblogger会在销毁时判断插件是否有'beforeUnolad'事件，这样会重复执行
    beforeunload = () => {
        if (!this.weblog) return;
        const now = new Date().valueOf();
        const moduleSection = this.generateModuleSection({
            type: 'PAGE_LEAVE',
            timestamp: now,
            stay: now - this.beginTime,
        }, '23');
        this.send(moduleSection);
        // 清空当前日志队列
        this.flush();
    }

    destroy = () => {
        removeMonitor(window, 'onpagehide' in window ? 'pagehide' : 'beforeunload', this.beforeunload);
        removeMonitor(window, urlChangeEventId, this.urlChange);
        if (isMobile()) {
            window.removeEventListener('touchstart', this.touchStartEvent, true);
            window.removeEventListener('touchmove', this.touchMoveEvent, true);
            window.removeEventListener('touchend', this.touchEndEvent, true);
        } else {
            // window.removeEventListener('click', this.clickEvent, true);
            window.removeEventListener('mousedown', this.mouseDownEvent, true);
            window.removeEventListener('mousemove', this.mouseMoveEvent, true);
            window.removeEventListener('mouseup', this.mouseUpEvent, true);
        }
        this.batchWaitTimer && clearTimeout(this.batchWaitTimer);

        window.removeEventListener('keydown', this.keyDownHandler);
        window.removeEventListener('gamepadconnected', this.gamepadConnectHandler);
    }
}
