import md5 from 'blueimp-md5';
import { pushException } from './risk-logger';

function getNavigatorPlugins(navigator: Navigator) : string {
    const plugins = navigator.plugins || [];
    if(plugins.length>0){
        const array: Array<Object> = [];
        for (let i = 0; i < plugins.length; i++) {
            const {name, filename, description} = plugins[i] || {};
            const json_data: Record<string, string | undefined> = {
                name,
                filename,
                description
            }; // 定义一个json对象
            array[i]=json_data;
         }
        return JSON.stringify(array);
    }
    return "";
}


function getNavigatorMimeTypes(navigator: Navigator) : string {
    const mimeTypes = navigator.mimeTypes || [];
    if(mimeTypes.length>0){
        const array : Array<Object> = [];
        for (let i = 0; i < mimeTypes.length; i++) {
            const { type, description } =mimeTypes[i] || {};
            const json_data = {
                type,
                description
            };// 定义一个json对象
            array[i]=json_data;
        }
        return JSON.stringify(array);
    }
    return "";
}


function getWeglData(document: Document) {
    try {
        var canvas = document.createElement('canvas');
        var gl = canvas.getContext('webgl');  // 等同于 canvas.getContext('experimental-webgl');
        if (gl != null) {
            var debugInfo = gl.getExtension('WEBGL_debug_renderer_info');
            if (debugInfo) {
                // GPU 制造商
                var vendor = gl.getParameter(debugInfo.UNMASKED_VENDOR_WEBGL);
                // GPU 型号
                var renderer = gl.getParameter(debugInfo.UNMASKED_RENDERER_WEBGL);
                let json_data: any = {};// 定义一个json对象
                json_data['61'] = vendor;
                json_data['62'] = renderer;

                var gl_vendor = gl.getParameter(gl.VENDOR);
                var gl_version = gl.getParameter(gl.VERSION);
                json_data['64'] = gl_vendor;
                json_data['65'] = gl_version;


                var shading_language_version = gl.getParameter(gl.SHADING_LANGUAGE_VERSION);
                var stencil_bits = gl.getParameter(gl.STENCIL_BITS);

                json_data['66'] = shading_language_version;
                json_data['67'] = stencil_bits;

                var gl_attributes = gl.getContextAttributes();
                if (gl_attributes != null) {
                    var antialias = gl_attributes.antialias ? '1' : '0';
                    json_data['63'] = antialias;
                }
                return json_data;
            }
        }
    } catch (err) {
        pushException(err);
    }
    return {};
}


var getWebglCanvas = function () {
    var canvas = document.createElement('canvas')
    var gl = null
    try {
        gl = canvas.getContext('webgl')
    } catch (e) {
        pushException(e);
    }
    if (!gl) {
        gl = null
    }
    return gl
}



// 由于iOS 支持从 8.1 版开始的 webgl，并且 8.1 版在多个图形芯片上运行，
// 因此不同 iOS 设备的结果可能不同，但我们需要对其进行验证

function getWeglFdData() {
    var gl = getWebglCanvas()
    if (!gl) { return null }
    var result = []
    //着色器
    var vShaderTemplate = 'attribute vec2 attrVertex;varying vec2 varyinTexCoordinate;uniform vec2 uniformOffset;void main(){varyinTexCoordinate=attrVertex+uniformOffset;gl_Position=vec4(attrVertex,0,1);}'
    var fShaderTemplate = 'precision mediump float;varying vec2 varyinTexCoordinate;void main() {gl_FragColor=vec4(varyinTexCoordinate,0,1);}'
    var vertexPosBuffer = gl.createBuffer()
    if (vertexPosBuffer != null) {
        gl.bindBuffer(gl.ARRAY_BUFFER, vertexPosBuffer)
        var vertices = new Float32Array([-0.2, -0.9, 0, 0.4, -0.26, 0, 0, 0.732134444, 0])
        gl.bufferData(gl.ARRAY_BUFFER, vertices, gl.STATIC_DRAW)
        var vertexPosBuffer_itemSize = 3
        var vertexPosBuffer_numItems = 3
        var program: any = gl.createProgram()
        var vshader = gl.createShader(gl.VERTEX_SHADER)

        if (vshader != null && program != null) {
            gl.shaderSource(vshader, vShaderTemplate)
            gl.compileShader(vshader)
            var fshader = gl.createShader(gl.FRAGMENT_SHADER)
            if (fshader != null) {
                gl.shaderSource(fshader, fShaderTemplate)
                gl.compileShader(fshader)
                gl.attachShader(program, vshader)
                gl.attachShader(program, fshader)
                gl.linkProgram(program)
                gl.useProgram(program)
                var program_vertexPosAttrib = gl.getAttribLocation(program, 'attrVertex')
                var program_offsetUniform = gl.getUniformLocation(program, 'uniformOffset')

                try {
                    gl.enableVertexAttribArray(program.vertexPosArray);
                    gl.vertexAttribPointer(program_vertexPosAttrib, vertexPosBuffer_itemSize, gl.FLOAT, !1, 0, 0)
                    gl.uniform2f(program_offsetUniform, 1, 1)
                    gl.drawArrays(gl.TRIANGLE_STRIP, 0, vertexPosBuffer_numItems)
                    var gl_canvas = gl.canvas as HTMLCanvasElement;
                    result.push(gl_canvas.toDataURL())
                } catch (e) {
                    /* .toDataURL may be absent or broken (blocked by extension) */
                    pushException(e);
                }
                return {
                    hash: md5(result[0]!),
                    rawData: result[0]
                };
            }
        }
    }
    return null;
}




function getCanvasFdData() {
    try {
        var canvas = document.createElement('canvas')
        canvas.width = 2000
        canvas.height = 200
        canvas.style.display = 'inline'
        var ctx = canvas.getContext('2d')
    
        if(ctx !=null){
            ctx.textBaseline = 'alphabetic'
            ctx.fillStyle = '#f60'
            ctx.fillRect(125, 1, 62, 20)
            ctx.fillStyle = '#069'

            ctx.font = '11pt Arial'
            ctx.globalCompositeOperation = 'multiply'
            ctx.fillStyle = 'rgb(255,255,0)'
            ctx.beginPath()
            ctx.arc(75, 100, 50, 0, Math.PI * 2, true)
            ctx.closePath()
            ctx.fill()
            ctx.fillStyle = 'rgb(255,0,255)'
            ctx.arc(75, 75, 75, 0, Math.PI * 2, true)
            ctx.arc(75, 75, 25, 0, Math.PI * 2, true)
            ctx.fill('evenodd');
            var canvasData = canvas.toDataURL();
            return {
                hash: md5(canvasData),
                rawData: canvasData
            };
        }
    } catch (e) {
        
    }
    return null;
}

var M = Math; // To reduce the minified code size
var fallbackFn = function () { return 0; };

function getMathFingerprint() {
    // Native operations
    var acos = M.acos || fallbackFn;
    var acosh = M.acosh || fallbackFn;
    var asin = M.asin || fallbackFn;
    var asinh = M.asinh || fallbackFn;
    var atanh = M.atanh || fallbackFn;
    var atan = M.atan || fallbackFn;
    var sin = M.sin || fallbackFn;
    var sinh = M.sinh || fallbackFn;
    var cos = M.cos || fallbackFn;
    var cosh = M.cosh || fallbackFn;
    var tan = M.tan || fallbackFn;
    var tanh = M.tanh || fallbackFn;
    var exp = M.exp || fallbackFn;
    var expm1 = M.expm1 || fallbackFn;
    var log1p = M.log1p || fallbackFn;
    // Operation polyfills
    var powPI = function (value: number) { return M.pow(M.PI, value); };
    var acoshPf = function (value: number) { return M.log(value + M.sqrt(value * value - 1)); };
    var asinhPf = function (value: number) { return M.log(value + M.sqrt(value * value + 1)); };
    var atanhPf = function (value: number) { return M.log((1 + value) / (1 - value)) / 2; };
    var sinhPf = function (value: number) { return M.exp(value) - 1 / M.exp(value) / 2; };
    var coshPf = function (value: number) { return (M.exp(value) + 1 / M.exp(value)) / 2; };
    var expm1Pf = function (value: number) { return M.exp(value) - 1; };
    var tanhPf = function (value: number) { return (M.exp(2 * value) - 1) / (M.exp(2 * value) + 1); };
    var log1pPf = function (value: number) { return M.log(1 + value); };
    // Note: constant values are empirical
    var mdata = {
        acos: acos(0.123124234234234242),
        acosh: acosh(1e308),
        acoshPf: acoshPf(1e154),
        asin: asin(0.123124234234234242),
        asinh: asinh(1),
        asinhPf: asinhPf(1),
        atanh: atanh(0.5),
        atanhPf: atanhPf(0.5),
        atan: atan(0.5),
        sin: sin(-1e300),
        sinh: sinh(1),
        sinhPf: sinhPf(1),
        cos: cos(10.000000000123),
        cosh: cosh(1),
        coshPf: coshPf(1),
        tan: tan(-1e300),
        tanh: tanh(1),
        tanhPf: tanhPf(1),
        exp: exp(1),
        expm1: expm1(1),
        expm1Pf: expm1Pf(1),
        log1p: log1p(10),
        log1pPf: log1pPf(10),
        powPI: powPI(-100),
    };

    return {
        h: md5(JSON.stringify(mdata)),
        d: mdata
    }
}

function getObjPropertys(obj: any) {

    let lc = 0;
    for (const key in obj) {
        lc++;
    }

    return {
        pc: Object.getOwnPropertyNames(obj).length,
        kc: Object.keys(obj).length,
        lc
    }
}

function getStorage() {
    let o = {};
    try {
        let lsc = window.localStorage.length;
        let ssc = window.sessionStorage.length;
        o = { lsc, ssc };
    } catch (error) {
        pushException(error);
    }
    return o;
}

function getHisLen() {
    let l = -1;
    try {
        l = window.history.length;
    } catch (error) {
        pushException(error);
    }
    return l;
}

function getCookieL() {
    let l = -1;
    try {
        l = document.cookie.split(";").length;
    } catch (error) {
        pushException(error);
    }
    return l;
}

function getFS() {
    let o = {};
    try {

        let isF = document.fullscreenElement || document['mozFullScreenElement' as keyof Document] || document['webkitFullscreenElement' as keyof Document];
        let en = document.fullscreenEnabled || document['mozFullScreenEnabled' as keyof Document] || document['webkitFullscreenEnabled' as keyof Document];

        o = {
            en: en ? true : false,
            isF: isF ? true : false
        }
    } catch (error) {
        pushException(error);
    }
    return o;
}

const permissionsNames = [
    "geolocation",
    "notifications",
    "push",
    "midi",
    "camera",
    "microphone",
    "speaker",
    "device-info",
    "background-fetch",
    "background-sync",
    "bluetooth",
    "persistent-storage",
    "ambient-light-sensor",
    "accelerometer",
    "gyroscope",
    "magnetometer",
    "clipboard",
    "display-capture",
    "nfc"
]

const getAllPermissions = async (callback: Function) => {
    const allPermissions: any[] = []
    await Promise.all(
        permissionsNames.map(async (permissionName) => {
            try {
                let permission
                switch (permissionName) {
                    case 'push':
                        // @ts-ignore
                        permission = await navigator.permissions.query({ name: permissionName, userVisibleOnly: true })
                        break
                    default:
                        permission = await navigator.permissions.query({ name: permissionName as PermissionName })
                }
                allPermissions.push({ permissionName, state: permission.state })
            }
            catch (e) {
                allPermissions.push({ permissionName, state: 'error'})
            }
        })
    ).then((values)=>{
        callback && callback(allPermissions);
    });
}

const getLocation = (callback: Function)=>{

    try {
        navigator.permissions.query({name:'geolocation'}).then(function(result) {

            if (result.state == 'granted') {
                // 获取地理位置信息
                navigator.geolocation.getCurrentPosition(position => {
                    // console.log('当前地理位置', position);
                    
                    callback && callback({
                        lat: position.coords.latitude,
                        lon: position.coords.longitude,
                    });
                  })
            }
        });
    } catch (error) {
        pushException(error);
    }
}

export {
    getNavigatorPlugins,
    getNavigatorMimeTypes,
    getWeglFdData,
    getWeglData,
    getCanvasFdData,
    getMathFingerprint,
    getObjPropertys,

    getCookieL,
    getStorage,
    getHisLen,
    getFS,

    getAllPermissions,
    getLocation,
};